<template>
    <el-container direction="vertical">
        <div align="center">
            <el-button type="success" @click="dialogFormVisible = true" style="width:150px;" plain></el-button>
        </div>
        <router-view></router-view>
    


    <el-dialog title="" :visible.sync="dialogFormVisible">
        <el-form :model="form">
            <el-form-item label="">
                <el-radio-group v-model="role" @change="selectRole">
                    <el-radio v-for="role in roleList" :key="role.roleId" :label="role.roleId" border>{{role.roleName}}</el-radio>
                </el-radio-group>
            </el-form-item>
            <el-form-item label="" >
          <el-cascader
          v-show="!isAuthority"
          placeholder=""
          :options="regions4"
          filterable
          :clearable=true
          @change="selected">
          </el-cascader>

          <el-cascader
          v-show="isAuthority"
          placeholder=""
          :options="regions3"
          filterable
          :clearable=true
          @change="selected">
          </el-cascader>
        </el-form-item>

            <!--  -->
            <el-form-item label="" v-show = flag >
                <template>
                    <el-table
                    :data="tableData"
                    style="width: 100%">
                        <el-table-column
                            prop="account"
                            label=""
                            width="180">
                        </el-table-column>
                        <el-table-column
                            prop="password"
                            label=""
                            width="180">
                        </el-table-column>
                        <el-table-column
                            prop="type"
                            label="">
                        </el-table-column>
                        <el-table-column
                            prop="addressId"
                            label="">
                        </el-table-column>
                    </el-table>
                </template>
            </el-form-item>
                
            
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormVisible = false"> </el-button>
            <el-button type="primary" @click="generateAccount" :loading="isload"> </el-button>
        </div>
    </el-dialog>

    </el-container>
</template>

<script>
export default {
    data() {
        return {
            isload:false,
            dialogFormVisible: false,
            //
            flag: false,
            //
            isAuthority: false,
            //
            tableData: [{
                account: '',
                password: '',
                type: '',
                addressId: ''
            }],

            role: '2',
            //
            form: {
                
                region:{
                    regionId:'',
                },
                
            },
            //
            regions3: [],
           //
            regions4:[],
            //
            roleList: []
        }
    },

    //
    watch: {
      role(value){
        if(value == 3){
          this.isAuthority = true
        }
        else{
          this.isAuthority = false
        }
      }
    },
    methods: {
        //
        selectRole(data){
            console.log(data)
            this.role = data
        },

        //
        selected(e){
            if(this.role == 3){
            this.form.region.regionId = e[2]
            }
            else{
            this.form.region.regionId = e[3]
            }  
        },
        

        //
        generateAccount(){
            this.isload = true
            this.axios.post("/generate/generateAccount",
            {roleId:this.role,regionId:this.form.region.regionId})
            .then((res)=>{
                this.isload = false

                 //
                this.tableData[0].account = res.data.data.admin.adminAccount
                this.tableData[0].password = '123456'
                try{
                    this.roleList.forEach(item=>{
                        if(item.roleId === this.role){
                            this.tableData[0].type = item.roleName
                            //
                            throw new Error("")
                        }
                    })  
                 }
                catch{
                }
                this.tableData[0].addressId = this.form.region.regionId

                this.flag = true
                this.$message({
                    type: 'success',
                    message:res.data.msg
                });

               

            })
            .catch((res)=>{
                console.log(res)
                this.isload = false
                type: 'error',
                this.$message({
                    message:res.data.msg
                });
            })

            
        },

        toUserManage(){
            this.$router.push({path:'/home/generateAdminAccount/userManage'})
        }
        
    },

    //
    created() {
        this.toUserManage()

         //
        this.axios.get("/admin/getAllRole")
        .then((res)=>{
            this.roleList = res.data.data.roles
        })
        .catch((res)=>{

        })

        this.axios.get("/admin/regionList")
        .then((res)=>{
            console.log(res);
            if(res.data.status == 0){
                this.regions4 = res.data.data.regionList
                this.regions3 = res.data.data.regionTreesForThree
            }
            else{
                this.$message({
                    message:res.data.msg
                });
            } 
        })
        .catch((res)=>{
            console.log(res);
        })
    }
    
}
</script>