<template>
    <el-container>
        <el-header>
            <!--  -->
            <el-menu :router=true :default-active="activeIndex"  class="el-menu-demo" mode="horizontal" @select="handleSelect">
            <el-submenu index="1">
                <template slot="title"></template>
                <el-menu-item index="/home/examineList"></el-menu-item>
                <el-menu-item index="/home/historyExamineList"></el-menu-item>
            </el-submenu>
            
            <el-submenu v-if="show" index="2">
                <template slot="title"></template>
                <el-menu-item index="/home/addCommunity"></el-menu-item>
                <el-menu-item index="/home/generateAdminAccount"></el-menu-item>
            </el-submenu>
            <el-submenu class="right" index="3">
                <template slot="title">{{region}}:{{roles[0].roleName}}</template>
                <el-menu-item @click="updatePassword"></el-menu-item>
            </el-submenu>
            </el-menu>
        </el-header>
        <el-main>
            <router-view></router-view>
        </el-main>
    </el-container>
</template>

<script>
export default {
    data() {
        return {
            activeIndex: '/home/examineList',
            region:'',
            roles:null,
            show:false,

        }
    },
    methods: {
        handleSelect(key, keyPath) {
            console.log(key, keyPath);
        },
        //
        setComData(){
            this.region = sessionStorage.getItem('region')
            this.roles = JSON.parse(sessionStorage.getItem('roles'))
            this.roles.forEach(element => {
                if(element.roleName===""){
                    this.show = true
                }
                
            })
        },
        updatePassword(){
            this.$prompt('', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                closeOnClickModal:false,
                inputPlaceholder:''
                }).then(({ value }) => {
                    console.log(value)
                    
                    this.axios.post('/admin/updatePassword',{adminPassword:value})
                    .then((res)=>{
                        console.log(res)
                            //
                            this.$message({
                                message:res.data.msg
                            });
                            this.$router.push({name:'/'})
                    }).catch((res)=>{
                        console.log(res)
                    })


                }).catch(() => {
                });
        }
    },
    created() {
        //
        this.setComData()
        //
        var adminModify = sessionStorage.getItem("adminModify")
        console.log(adminModify)
        if(adminModify === '0'){
            this.$prompt('', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                closeOnClickModal:false,
                inputPlaceholder:''
                }).then(({ value }) => {
                    console.log(value)
                    
                    this.axios.post('/admin/updatePassword',{adminPassword:value})
                    .then((res)=>{
                        console.log(res)
                            //
                            this.$message({
                                message:res.data.msg
                            });
                            this.$router.push({name:'/'})
                    }).catch((res)=>{
                        console.log(res)
                    })


                }).catch(() => {
                    this.$router.push({name:'/'})
                });
        }

    }

}
</script>

<style scoped>
  .right{
    position: absolute;
    right: 0px;
  }
  .right-2{
    position: absolute;
    right: 80px;
  }
</style>