<template>
    <el-container direction="vertical">
    <el-main>
    <el-dialog title="" :visible.sync="dialogFormVisible" width="40%">
      <el-form ref="form" :model="form" label-width="80px">
        <el-form-item label="" >
          <el-input v-model="form.adminAccount" disabled ></el-input>
        </el-form-item>
        <el-form-item label="" >
          <el-input v-model="form.adminPassword" placeholder="" ></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="role" placeholder="">
            <el-option v-for="role in roleList" :key="role.roleId" :label="role.roleName" :value="role.roleId"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" >
          <el-cascader
          v-show="!isAuthority"
          placeholder=""
          :options="regions4"
          filterable
          :clearable=true
          @change="selected">
          </el-cascader>

          <el-cascader
          v-show="isAuthority"
          placeholder=""
          :options="regions3"
          filterable
          :clearable=true
          @change="selected">
          </el-cascader>
        </el-form-item>
          
        <el-form-item>
          <el-button type="primary" @click="openUpdateWindow()"></el-button>
          <el-button  @click="dialogFormVisible = false"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>

    <!--  -->
    <el-table
      :data="showList.slice((currentPage-1)*pageSize,currentPage*pageSize)"
      style="width: 100%"
      height="460">
      
      <el-table-column
      type="index"
      label=""
      width="50">
      </el-table-column>
      <el-table-column
        prop="adminAccount"
        label=""
        width="180">
      </el-table-column>
      
      <el-table-column
        prop="roles[0].roleName"
        label=""
        width="180">
      </el-table-column> 
      <el-table-column
        prop="region.fullName"
        label="">
      </el-table-column>
      <el-table-column
        prop="adminModify"
        label=""
        width="180">
      </el-table-column> 
      
      <el-table-column
      align="right">
      
        <template slot="header" slot-scope="scope">
          <el-input
            v-model="search"
            size="mini"
            placeholder="" />
        </template>
        <template slot-scope="scope" >
          <el-button
            size="mini"
            type="warning"
            plain
            @click="updateForm(scope.row)"></el-button>
          <el-button
            size="mini"
            type="danger"
            plain
            @click="openDeleteWindow(scope.row)"></el-button>
        </template>
      </el-table-column>
      
    </el-table>

    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-sizes="[5, 10, 15, 20]"
      :page-size="pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="adminList.length"
      prev-text=""
      next-text=""
      style="
        text-align:center;
        letter-spacing:4px
      "
    >
    </el-pagination>
    </el-main>
    </el-container>

</template>
<script>
export default {
    computed:{
        showList:function(){
            return this.adminList.filter((item)=>{
                item.adminModify == 0 || item.adminModify == "" ? item.adminModify='' : item.adminModify=''
                if(item.adminAccount.includes(this.search)||item.adminModify.includes(this.search)||(item.region!= null && item.region.fullName.includes(this.search))||item.roles[0].roleName.includes(this.search) ){
                   return item;
                }
            })
        },


    },
    data(){
        return{
          dialogFormVisible: false,
          //
          currentPage: 1,
          //
          pageSize: 5,
          adminList: [],
          search:'',
          adminId: '',
          dialogVisible: false,
          //
          isAuthority: false,
          //
          regions3: [],
           //
          regions4:[],
          //
          roleList: [],
          role: '',
          //
          form: {
            
            adminAccount: '',
            adminPassword: '',
            region:{
                  regionId:'',
            },

          }


        }
    },

    //
    watch: {
      role(value){
        if(value == 3){
          this.isAuthority = true
        }
        else{
          this.isAuthority = false
        }
      }
    },

    //
    methods: {
        loadAdminList(){
            this.axios.get("/admin/adminList")
            .then((res)=>{
                this.adminList = res.data
            })
            .catch((res)=>{

            })
        },

        //
        updateForm(data){
          this.dialogFormVisible = true
          this.form.adminAccount = data.adminAccount
          this.role = data.roles[0].roleName
          this.form.region.regionId = data.region.regionId
          this.adminId = data.adminId

        },

        //
        deleteAdmin(data){
          this.axios.get("/admin/deleteAdmin/" + data.adminId)
          .then((res)=>{

            // 
            this.adminList.forEach((admin,index) => {
              if(admin.adminId == data.adminId){
                //
                this.adminList.splice(index,1)
              }
            });
            this.$message({
              type: 'success',
              message:res.data.msg
            });

          })
          .catch((res)=>{
            this.$message({
              type: 'error',
              message:res.data.msg
            });
          })

        },

        //
        openDeleteWindow(data) {
          this.$confirm(', ?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(() => {
            this.deleteAdmin(data)

          }).catch(() => {  

          });
        },

        //
        openUpdateWindow() {
          this.$confirm(', ?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(() => {
            this.onSubmit()
          }).catch(() => {  

          });
        },

        //
        onSubmit() {
          this.axios.post("/admin/updateAdmin",{
            adminId: this.adminId,
            adminAccount: this.form.adminAccount,
            adminPassword: this.form.adminPassword,
            region: {regionId:this.form.region.regionId},
            roles: [{roleId:this.role}]
          })
          .then((res)=>{
            this.$message({
              type: 'success',
              message:res.data.msg
            });
            this.dialogFormVisible = false

            //
            this.adminList.forEach((admin,index)=>{
              if(admin.adminId == res.data.data.admin.adminId){
                this.adminList.splice(index,index,res.data.data.admin)
              }
            })

            
          })
          .catch((res)=>{
             this.$message({
              type: 'error',
              message:res.data.msg
            });
          })

        },

        //
        selected(e){
          if(this.role == 3){
            this.form.region.regionId = e[2]
          }
          else{
            this.form.region.regionId = e[3]
          } 
        },

        //
        handleSizeChange: function(size){
          this.pageSize = size

        },

        //
        handleCurrentChange: function(currentPage){
            this.currentPage = currentPage
        },
    },

    created(){
      this.loadAdminList()

      //
      this.axios.get("/admin/getAllRole")
      .then((res)=>{
        console.log(res)
        this.roleList = res.data.data.roles
      })
      .catch((res)=>{

      })

      //
      this.axios.get("/admin/regionList")
      .then((res)=>{
        if(res.data.status == 0){
            this.regions4 = res.data.data.regionList
            this.regions3 = res.data.data.regionTreesForThree
        }
        else{
            this.$message({
                message:res.data.msg
            });
        } 
      })
      .catch((res)=>{
          console.log(res);
      })
    },
}
</script>