<template>
    <el-container >
        <el-header class="right">
            <el-button type="text" icon="el-icon-info" @click="attentionDialogVisible = true"></el-button>
            <el-button  type="success" @click="addCommunityFormVisible = true"></el-button>
        </el-header>
        
        <el-main>
            <el-dialog
            title=""
            :visible.sync="attentionDialogVisible"
            width="50%">
            <p>1</p>
            <p>2</p>
            <p>3</p>
            <p>4<span class="text-color-red"></span></p>
            <span slot="footer" class="dialog-footer">
                <el-button type="text" @click="displayAttention"></el-button>
                <el-button type="primary" @click="attentionDialogVisible = false"> </el-button>
            </span>
            </el-dialog>
        <!--  -->
        <el-dialog title="" :visible.sync="addCommunityFormVisible">
        <el-form :model="form" :rules="commRules" ref="comm">
            <el-form-item label="" :label-width="formLabelWidth" prop="communityName">
            <el-input v-model="form.communityName" autocomplete="off"></el-input>
            </el-form-item>
             <el-form-item label="" :label-width="formLabelWidth" prop="communityInfo">
            <el-input v-model="form.communityInfo" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="" :label-width="formLabelWidth" prop="region.regionId">
                <el-cascader
                placeholder=""
                :options="options"
                filterable
                :clearable=true
                @change="selected">
                </el-cascader>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button @click="clearCommunityInfo"> </el-button>
            <el-button type="primary" @click="addCommunity('comm')" :loading="addCommunityIsload"> </el-button>
        </div>
        </el-dialog>
        <!--  -->
        <el-dialog title=""  :visible.sync="addRoomFormVisible">
            <el-form :model="room" :rules="roomRules" ref="roomFrom">
                <el-form-item label="" :label-width="formLabelWidth" prop="roomName" >
                <el-input v-model="room.roomName" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth" prop="house.houseId" >
                <el-select v-model="room.house.houseId" clearable placeholder="">
                    <el-option
                    v-for="item in houseList"
                    :key="item.houseId"
                    :label="item.houseName"
                    :value="item.houseId">
                    </el-option>
                </el-select>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="clearRoomInfo"> </el-button>
                <el-button type="primary" @click="toAddRoom('roomFrom')" :loading="addRoomIsload"> </el-button>
            </div>
        </el-dialog>
        <!--  -->
        <el-dialog title="" :visible.sync="updateCommunityFormVisible">
            <el-form :model="preUpdateCommuntiy" :rules="commRules" ref="upComm">
                <el-form-item label="" :label-width="formLabelWidth" prop="communityName">
                    <el-input v-model="preUpdateCommuntiy.communityName" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth" prop="communityInfo">
                    <el-input v-model="preUpdateCommuntiy.communityInfo" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth" prop="region.regionId">
                <el-cascader
                    placeholder=""
                    :options="options"
                    v-model="preUpdateCommuntiy.region.regionId"
                    filterable
                    :clearable=true
                    @change="setpreUpdateCommuntiyRegion">
                    </el-cascader>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="clearUpdateCommunityFormVisible"> </el-button>
                <el-button type="primary" @click="updateCommunityCommit('upComm')" :loading="updateCommunityLoad"> </el-button>
            </div>
        </el-dialog>

    <!--  -->
    <el-table
      :data="showList.slice((currentPage-1)*pageSize,currentPage*pageSize)"
      style="width: 100%"
      height="530">
          <el-table-column
      type="index"
      label=""
      width="50">
    </el-table-column>
            <el-table-column
        prop="communityName"
        label=""
        width="180">
      </el-table-column>
      <el-table-column
        prop="communityInfo"
        label=""
        width="180">
      </el-table-column> 
    <el-table-column
        prop="region.fullName"
        label="">
      </el-table-column>
      <el-table-column
      align="right">
      <template slot="header" slot-scope="scope">
        <el-input
          v-model="search"
          size="mini"
          placeholder=""/>
      </template>
      <template slot-scope="scope" >
        <el-button
          size="mini"
          @click="addHouse(scope.$index, scope.row)"></el-button>
        <el-button
          size="mini"
          @click="addRoom(scope.$index, scope.row)"></el-button>
          <el-button
          size="mini"
          type="primary"
          plain
          @click="selectCommunityInfo(scope.$index, scope.row)"></el-button>
        <el-button
          size="mini"
          type="warning"
          plain
          @click="updateCommunity(scope.$index, scope.row)"></el-button>
        <el-button
          size="mini"
          type="danger"
          plain
          @click="deleteCommunity(scope.$index, scope.row)"></el-button>
      </template>
      </el-table-column>
    </el-table>
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-sizes="[5, 10, 15, 20]"
      :page-size="pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="showList.length"
      prev-text=""
      next-text=""
      style="
        text-align:center;
        letter-spacing:4px
      "
    >
    </el-pagination>
</el-main>
    </el-container>

</template>

<script>
export default {
    computed:{
        showList:function(){
            return this.communityList.filter((item)=>{
                if(item.communityName.includes(this.search)||item.communityInfo.includes(this.search)||(item.region!= null && item.region.fullName.includes(this.search))){
                    return item;
                }
            })
        }
    },
    data() {
        return {
                //
            currentPage: 1,
            //
            pageSize: 10,
            attentionDialogVisible:false,
            updateCommunityFormVisible:false,
            updateCommunityLoad:false,
            addCommunityIsload:false,
            addRoomIsload:false,
            addCommunityFormVisible: false,
            addRoomFormVisible:false,
            commRules:{
                communityName:[{required: true, message: '', trigger: 'blur'}],
                communityInfo:[{required: true, message: '', trigger: 'blur'}],
                region:{
                    regionId:[{required: true, message: '', trigger: 'blur'}]  
                }
            },
            form: {
                communityName:'',
                communityInfo:'',
                region:{
                    regionId:'',
                }
            },
            roomRules:{
                roomName:[{required: true, message: '', trigger: 'blur'}],
                house:{
                    houseId:[{required: true, message: '', trigger: 'blur'}]
                }
            },
            room:{
                roomName:'',
                house:{
                    houseId:''
                }
            },
            formLabelWidth: '120px',
            options:[],
            communityList:[],
            search:'',
            houseList:[],
            preUpdateCommuntiy:{
                communityId:'',
                communityName:'',
                communityInfo:'',
                region:{
                    regionId:'',
                }
            }
        }
    },
    methods: {
         //
        handleSizeChange: function(size){
          this.pageSize = size

        },

        //
        handleCurrentChange: function(currentPage){
            this.currentPage = currentPage
        },
        //
        displayAttention(){
            localStorage.setItem("showAttention",JSON.stringify(false))
            this.attentionDialogVisible = false
        },
        setpreUpdateCommuntiyRegion(e){
            console.log(e)
            if(e.length==4){
                this.preUpdateCommuntiy.region.regionId = e[3]
            }
            else{
                this.preUpdateCommuntiy.region.regionId = null
            }
        },
        updateCommunityCommit(formName){
            this.$refs[formName].validate((valid) => {
            if (valid) {
                this.updateCommunityLoad = true
                console.log(this.preUpdateCommuntiy)
                this.axios.post('admin/updateCommunity',this.preUpdateCommuntiy)
                .then((res)=>{
                        if(res.data.status == 0){
                            //
                            this.$message({
                                type: 'success',
                                message: res.data.msg
                            });
                            this.loadCommunityList()
                        }else{
                            //
                            this.$message({
                                type: 'error',
                                message: res.data.msg
                            });
                        }
                        this.updateCommunityLoad = false
                        this.updateCommunityFormVisible = false
                })
                .catch((res)=>{
                    this.$message({
                        type: 'warning',
                        message: ""
                    });  
                    this.updateCommunityLoad = false 
                })
                
            } else {
                console.log('error submit!!');
                return false;
            }
            });
            
        },
        clearUpdateCommunityFormVisible(){
            this.preUpdateCommuntiy.communityId = ''
            this.preUpdateCommuntiy.communityName = ''
            this.preUpdateCommuntiy.communityInfo = ''
            this.preUpdateCommuntiy.region.regionId = ''
            this.updateCommunityFormVisible = false

        },
        updateCommunity(index,row){
            console.log(row)
            this.updateCommunityFormVisible = true
            this.preUpdateCommuntiy.communityId = row.communityId
            this.preUpdateCommuntiy.communityName = row.communityName
            this.preUpdateCommuntiy.communityInfo = row.communityInfo
            this.preUpdateCommuntiy.region.regionId = row.region.regionId

            console.log(this.preUpdateCommuntiy)
        },
        deleteLocalCommunityData(communityId){
            this.communityList.forEach((item,index)=>{
            if(item.communityId == communityId){
                this.communityList.splice(index,1)
                return;
            }
            })
        },
        deleteCommunity(index,row){
            this.$confirm(', ?!', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
            }).then(() => {
            this.axios.post('/admin/deleteCommunity',{communityId:row.communityId})
            .then((res)=>{
                if(res.data.status==0){
                    //
                    this.deleteLocalCommunityData(row.communityId)
                    this.$message({
                        type: 'success',
                        message: res.data.msg
                    });   
                }else{
                    this.$message({
                        type: 'error',
                        message: res.data.msg
                    });   
                }

            })
            .catch((res)=>{
                this.$message({
                        type: 'warning',
                        message: ""
                    });   
            })
            }).catch(() => {
            this.$message({
                type: 'info',
                message: ''
            });          
            });
        },
        selectCommunityInfo(index,row){
            console.log(index,row)
            sessionStorage.setItem('communityInfo',JSON.stringify(row))
            this.$router.push({name:'communityInfo'})
        },
        //
        clearRoomInfo(){
            this.room.roomName = ''
            this.room.house.houseId = ''
            this.addRoomFormVisible = false

        },
        toAddRoom(formName){
            this.$refs[formName].validate((valid) => {
            if (valid) {
                this.addRoomIsload = true
                console.log(this.room)
                this.axios.post('/admin/addRoom',this.room)
                .then((res)=>{
                    console.log(res)
                    if(res.data.status == 0){
                        this.$message({
                            type: 'success',
                            message: res.data.msg
                        });
                    }else{
                        this.$message({
                            type: 'error',
                            message: res.data.msg
                        });
                    }
                    
                    this.addRoomIsload = false
                    this.addRoomFormVisible = false
                    this.clearRoomInfo()
                })
                .catch((res)=>{
                    this.$message({
                        type: 'warning',
                        message: ""
                    }); 
                })

            } else {
                console.log('error submit!!');
                return false;
            }
            });
                    
            
        },
        //
        addRoom(index,row){
            //
            this.axios.get('/admin/houseList/' + row.communityId)
            .then((res)=>{
                if(res.data.status == 0){
                    this.houseList = res.data.data.houseList
                    //
                    this.addRoomFormVisible = true
                }else{
                    this.$message({
                        type: 'error',
                        message: res.data.msg
                    }); 
                }
                
            })
            .catch((res)=>{
                this.$message({
                        type: 'warning',
                        message: ""
                    }); 
            })
        },
        //
        addHouse(index,row){
            this.$prompt('', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            inputPattern:/\S/,
            inputErrorMessage: ''
            }).then(({ value }) => {
                    this.axios.post("/admin/addHouse",{houseName:value,community:{communityId:row.communityId}})
                    .then((res)=>{
                        if(res.data.status == 0){
                            this.$message({
                                type: 'success',
                                message: res.data.msg
                            }); 
                        }else{
                            this.$message({
                                type: 'error',
                                message: res.data.msg
                            }); 
                        }
                            
                    })
                    .catch((res)=>{
                        this.$message({
                            type: 'warning',
                            message: ""
                        }); 
                    })
            }).catch(() => {
            this.$message({
                type: 'info',
                message: ''
            });       
            });
            console.log(index, row);
        },
        //
        selected(e){
            console.log(e)
            if(e.length==4){
            this.form.region.regionId = e[3]
            }
            console.log(this.form.region.regionId)
        },
        clearCommunityInfo(){
            this.form.communityName = '',
            this.form.communityInfo = '',
            this.form.region.regionId = '',
            this.addCommunityFormVisible = false
        },
        //
        addCommunity(formName){
            this.$refs[formName].validate((valid) => {
            if (valid) {
                    this.addCommunityIsload = true
                    this.axios.post("/admin/addCommunity",this.form)
                    .then((res)=>{
                        if(res.data.status == 0){
                            this.communityList.push(res.data.data.dbData)
                            this.$message({
                                type:'success',
                                message:res.data.msg
                            });
                        }
                        else{
                            this.$message({
                                type:'error',
                                message:res.data.msg
                            });
                        }
                        this.addCommunityIsload = false
                        this.addCommunityFormVisible = false
                        
                    })
                    .catch((res)=>{

                        this.$message({
                            type:'warning',
                            message:""
                        });
                    })


            } else {
                console.log('error submit!!');
                return false;
            }
            });

            
        },
        //
        loadRegion(){
            this.axios.get("/admin/regionList")
            .then((res)=>{
                if(res.data.status == 0){
                    this.options = res.data.data.regionList
                    this.$message({
                        type:'success',
                        message:res.data.msg
                    });
                }
                else{
                    this.$message({
                        type:'error',
                        message:res.data.msg
                    });
                }
            })
            .catch((res)=>{
                this.$message({
                        type:'warning',
                        message:""
                    });
            })
        },
        //
        loadCommunityList(){
            this.axios.get("/admin/communityList")
            .then((res)=>{
                console.log(res);
                if(res.data.status == 0){
                    this.communityList = res.data.data.communityList
                    this.$message({
                        type:'success',
                        message:res.data.msg
                    });
                }
                else{
                    this.$message({
                        type:'error',
                        message:res.data.msg
                    });
                }
            })
            .catch((res)=>{
                this.$message({
                        type:'warning',
                        message:""
                    });
            })
        }

    },
    created() {
        this.loadRegion()
        this.loadCommunityList()
        this.attentionDialogVisible = JSON.parse(localStorage.getItem("showAttention"))
    }, 
}
</script>

<style scoped>
    .right{
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content:flex-end;
    }
    .text-color-red{
        color: red;
    }
</style>