<template>
  <el-container>
      <el-header>
          {{info.region.fullName}}:{{info.communityName}}
      </el-header>
      <main >
      <el-tabs v-model="activeName" @tab-click="handleClick" :tab-position="'left'" style="height: 500px;" >
        <el-tab-pane label="" name="first">
            <el-collapse v-model="activeNames" @change="handleChange">
              <el-collapse-item title="" name="1">
                <div></div>
                <div></div>
              </el-collapse-item>
              <el-collapse-item title="" name="2">
                <div></div>
                <div></div>
                <div><span class="text-color-red"></span></div>
              </el-collapse-item>
              <el-collapse-item title="" name="3">
                <div></div>
                <div></div>
                <div><span class="text-color-red"></span><span class="text-color-red"></span></div>
              </el-collapse-item>
              <el-collapse-item title="" name="4">
                <div><span class="text-color-red"></span></div>
                <div><span class="text-color-red"></span></div>
              </el-collapse-item>
            </el-collapse>
        </el-tab-pane>
        <el-tab-pane label="" name="second">
            <el-table
            :data="showHouseList"
            style="width: 60%"
            height="500">
            <el-table-column
              type="index">
            </el-table-column>
          <el-table-column
              prop="community.communityName"
              label=""
              width="280">
            </el-table-column>
            <el-table-column
              prop="houseName"
              label=""
              width="180">
            </el-table-column>
            <el-table-column
            align="right">
            <template slot="header" slot-scope="scope">
              <el-input
                v-model="search"
                size="mini"
                placeholder=""/>
            </template>
            <template slot-scope="scope">
              <el-button
                size="mini"
                type="warning"
                plain
                @click="updateHouse(scope.$index, scope.row)"></el-button>
              <el-button
                size="mini"
                type="danger"
                plain
                @click="deleteHouse(scope.$index, scope.row)"></el-button>
            </template>
          </el-table-column>
          </el-table>
        </el-tab-pane>
        <el-tab-pane label="" name=" three">
          <el-table
            :data="showRoomList"
            style="width: 60%"
            height="500">
            <el-table-column
              type="index">
            </el-table-column>
          <el-table-column
              prop="house.houseName"
              label=""
              width="180">
            </el-table-column>
           <el-table-column
              prop="roomName"
              label=""
              width="180">
            </el-table-column>
            <el-table-column
              label=""
              width="180">
              <template slot-scope="scope">
                <el-tag
                  :type="scope.row.owner == null ? 'warning' : 'success'"
                  disable-transitions>{{scope.row.owner== null ? '' : ''}}</el-tag>
              </template>
            </el-table-column>
            <el-table-column
            align="right">
            <template slot="header" slot-scope="scope">
                <el-select v-model="value" size="mini" clearable placeholder="">
                <el-option
                  v-for="item in houseList"
                  :key="item.houseId"
                  :label="item.houseName"
                  :value="item.houseId">
                </el-option>
              </el-select>
            </template>
            <template slot-scope="scope">
              <el-button
                size="mini"
                type="warning"
                plain
                @click="updateRoom(scope.$index, scope.row)"></el-button>
              <el-button
                size="mini"
                type="danger"
                plain
                @click="deleteRoom(scope.$index, scope.row)"></el-button>
            </template>
          </el-table-column>
          </el-table>
        </el-tab-pane>
        <el-tab-pane label="" name=" four">
          <el-table
          :data="candidateList"
          style="width: 100%">
          <el-table-column
            label=""
            type="index">
          </el-table-column>
          <el-table-column
            label=""
            prop="owner.ownerName">
          </el-table-column>
          <el-table-column
            label=""
            prop="candidatePoll">
          </el-table-column>
          <el-table-column
            label=""
            prop="candidateCreateTime">
            <template slot-scope="scope">
              {{scope.row.candidateCreateTime | dateFormart}}
            </template>
          </el-table-column>
          <el-table-column
            align="right">
            <template slot="header" slot-scope="scope">
              <el-button type="success" @click="endVote" ></el-button>
              <el-button type="primary" @click="getunCandidateOwnerList"></el-button>
            </template>
            <template slot-scope="scope">
              <el-button
                size="mini"
                type="danger"
                @click="deleteCandidate(scope.$index, scope.row)" plain=""></el-button>
            </template>
          </el-table-column>
        </el-table>
        <el-dialog title=""  :visible.sync="addCandidateDialogVisible">
          <span></span>
          <el-select v-model="value1" multiple placeholder="" >
            <el-option
              v-for="item in ownerList"
              :key="item.ownerId"
              :label="item.ownerName"
              :value="item.ownerId">
            </el-option>
          </el-select>
          <div slot="footer" class="dialog-footer">
            <el-button @click="addCandidateDialogVisible = false"> </el-button>
            <el-button type="primary" @click="addCandidate"> </el-button>
          </div>
        </el-dialog>
        </el-tab-pane>
        <el-tab-pane label="" name=" five">
           <boardMember></boardMember>
         </el-tab-pane>
        <el-tab-pane label="" name="six" >
          <div v-for="item in communityAccount" :key="item.managerId">
            {{item.type==2?'':''}}
            <div class="info">
              <el-tag>{{item.account}}</el-tag> 
            </div>
            <div class="info">
               <el-tag type="success">{{item.password}}</el-tag>
            </div>
          </div>
        </el-tab-pane>
         
    </el-tabs>
  </main>
  </el-container>
</template>

<script>
import boardMember from './boardMember'

export default {
  components:{
    'boardMember':boardMember
  },
  computed:{
    showHouseList:function(){
      return this.houseList.filter((item)=>{
        if(item.houseName.includes(this.search)){
          return item
        }
      })
    },
    showRoomList:function(){
      return this.roomList.filter((item)=>{
        if(item.house.houseId.includes(this.value)){
          return item
        }
      })
    }
  },
  data() {
        return {
          value1: [],
          addCandidateDialogVisible:false,
          activeNames: ['1','2','3','4'],
          search:'',
          activeName: 'first',
          info:'',
          houseList:[],
          roomList:[],
          ownerList:[],
          candidateList:[],
          value:'',
          communityAccount:[]
        }
    },
    methods: {
      //
      endVote(){
        this.$prompt('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          inputType:'number',
          inputErrorMessage: ''
        }).then(({ value }) => {
          this.axios.post('/admin/createCommittee/'+this.info.communityId+'/'+value)
          .then((res)=>{
            console.log(res)
            if(res.data.status == 0){
              this.$message({
                type: 'success',
                message: res.data.msg
              }); 
              this.getCandidateList(this.info.communityId)
            }
          })
          .catch((res)=>{
              this.$message({
                type: 'error',
                message: res.data.msg
              }); 
          })


        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });       
        });
      },
      deleteCandidate(index,row){
        this.$confirm(',, ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.axios.get("/admin/deleteCandidate/"+row.candidateId)
          .then((res)=>{
            if(res.data.status == 0){
              this.candidateList.forEach((item,index)=>{
                  if(item.candidateId === row.candidateId){
                    this.candidateList.splice(index,1)
                    return;
                  }
              })

               this.$message({
                  type: 'success',
                  message: res.data.msg
                });
            }
            else{
              this.$message({
                  type: 'error',
                  message: res.data.msg
                });
            }
          })
          .catch((res)=>{
                this.$message({
                  type: 'warning',
                  message: '!'
                });
          })


         
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });          
        });

        console.log(row)
      },
      getunCandidateOwnerList(){
        this.addCandidateDialogVisible = true
        this.axios.get('/admin/unCandidateOwnerList/'+this.info.communityId)
        .then((res)=>{
             console.log(res)
            if(res.data.status == 0){
              this.ownerList = res.data.data.ownerList
              console.log(this.ownerList)
            }
            else{
              this.ownerList = []
            }
        })
        .catch((res)=>{
              this.ownerList = []
        })
      },
      addCandidate(){
        console.log(this.value1)
        //
        this.axios.post('/admin/addCandidate',{ownerId:this.value1,communityId:this.info.communityId})
        .then((res)=>{

          if(res.data.status == 0){
            res.data.data.candidateList.forEach((item)=>{
              this.candidateList.push(item)
            })


            this.$message({
              type:'success',
              message:res.data.msg
            })
          }
          else{
            this.$message({
              type:'error',
              message:res.data.msg
            })
          }
        })
        .catch((res)=>{
          this.$message({
              type:'warning',
              message:""
            })
        })


        this.addCandidateDialogVisible = false
      },
      handleChange(val) {
        console.log(val);
      },
      deleteLocalHouseData(houseId){
        this.houseList.forEach((item,index)=>{
          if(item.houseId == houseId){
              this.houseList.splice(index,1)
              return;
          }
        })
      },
      deleteHouse(index,row){
        this.$confirm(', ?!', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.axios.post('/admin/deleteHouse',{houseId:row.houseId})
          .then((res)=>{
              if(res.data.status==0){
                //
                  this.deleteLocalHouseData(row.houseId)
                  this.$message({
                    type: 'success',
                    message: res.data.msg
                  });   
              }else{
                  this.$message({
                    type: 'error',
                    message: res.data.msg
                  });
              }
          })
          .catch((res)=>{
            this.$message({
              type: 'warning',
              message: ""
            });   
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });          
        });
      },
      deleteLocalRoomData(roomId){
        this.roomList.forEach((item,index)=>{
          if(item.roomId == roomId){
              this.roomList.splice(index,1)
              return;
          }
        })
      },
      deleteRoom(index,row){
        this.$confirm(', ?!,', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          //
          if(row.owner != null){
            this.$message({
                type: 'warning',
                message: ""
              });
              return;
          }
          this.axios.post('/admin/deleteRoom',{roomId:row.roomId})
          .then((res)=>{
              if(res.data.status==0){
                //
                  this.deleteLocalRoomData(row.roomId)
                  this.$message({
                    type: 'success',
                    message: res.data.msg
                  });   
              }
              else{
                this.$message({
                    type: 'error',
                    message: res.data.msg
                  });   
              }
          })
          .catch((res)=>{
            this.$message({
                type: 'error',
                message: ""
              });
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });          
        });
        
      },
      updateLocalRoomData(roomId,roomName){
        this.roomList.forEach((item)=>{
          if(item.roomId===roomId){
            item.roomName = roomName
          }
        })
      },
      updateRoom(index,row){
        console.log(index,row);
            this.$prompt('', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                inputPattern:/\S/,
                inputErrorMessage: ''
              }).then(({ value }) => {
                this.axios.post('/admin/updateRoom',{roomId:row.roomId,roomName:value})
                .then((res)=>{
                    if(res.data.status==0){
                      //
                        this.updateLocalRoomData(row.roomId,value)
                        this.$message({
                          type: 'success',
                          message: res.data.msg
                        });   
                    }
                    else{
                      this.$message({
                          type: 'error',
                          message: res.data.msg
                        });   
                    }
                })
                .catch((res)=>{
                  this.$message({
                          type: 'error',
                          message: ""
                        });   
                })
              }).catch(() => {
                this.$message({
                  type: 'info',
                  message: ''
                });       
              });
      },
      updateLocalHouseData(houseId,houseName){
        this.houseList.forEach((item)=>{
          if(item.houseId===houseId){
            item.houseName = houseName
          }
        })
      },
      //
        updateHouse(index,row){
            console.log(index,row);
            this.$prompt('', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                inputPattern:/\S/,
                inputErrorMessage: ''
              }).then(({ value }) => {
                this.axios.post('/admin/updateHouse',{houseId:row.houseId,houseName:value})
                .then((res)=>{
                    if(res.data.status==0){
                      //
                        this.updateLocalHouseData(row.houseId,value)
                        this.$message({
                          type: 'success',
                          message: res.data.msg
                        });   
                    }
                    else{
                      this.$message({
                          type: 'error',
                          message: res.data.msg
                        });   
                    }
                })
                .catch((res)=>{
                  this.$message({
                          type: 'error',
                          message: ""
                        });   
                })
              }).catch(() => {
                this.$message({
                  type: 'info',
                  message: ''
                });       
              });
        },
        handleClick(tab, event) {
            console.log(tab, event);
        },
        //
        getRoom(communityId){
          this.axios.get('/admin/roomList/'+communityId)
            .then((res)=>{
              console.log(res)
              if(res.data.status==0){
                this.roomList = res.data.data.roomList
                this.$message({
                    type: 'success',
                    message: res.data.msg
                });
              }
              else{
                this.$message({
                    type: 'error',
                    message: res.data.msg
                });
              }

            })
            .catch((res)=>{
              this.$message({
                    type: 'warning',
                    message: ""
                });
            })
        },
        //
        getHouse(communityId){
            this.axios.get('/admin/houseList/'+communityId)
            .then((res)=>{
              console.log(res)
              if(res.data.status==0){
                this.houseList = res.data.data.houseList
                this.$message({
                    type: 'success',
                    message: res.data.msg
                });
              }
              else{
                this.$message({
                    type: 'error',
                    message: res.data.msg
                });
              }

            })
            .catch((res)=>{
              this.$message({
                    type: 'warning',
                    message: ""
                });
            })
        },
        getCandidateList(communityId){
          this.axios.get("/admin/candidateList/"+communityId)
          .then((res)=>{
            if(res.data.status == 0){
              console.log(res)
              this.candidateList = res.data.data.candidateList

              this.$message({
                type:'success',
                message:res.data.msg
              })
            }
            else{
              this.$message({
                type:'error',
                message:res.data.msg
              })
            }
          })
          .catch((res)=>{
            this.$message({
              type:'warning',
              message:''
            })
          })

        },
        getCommunityAccount(communityId){
          this.axios.get('/admin/getCommunityAccount/'+communityId)
          .then((res)=>{
            console.log(res)
              if(res.data.status == 0){
                this.communityAccount = res.data.data.communityAccount
              }
          })
          .catch((res)=>{

          })
        }
    },
    created() {
        //
        this.info = JSON.parse(sessionStorage.getItem('communityInfo'))
        this.getHouse(this.info.communityId)
        this.getRoom(this.info.communityId)
        this.getCandidateList(this.info.communityId)
        this.getCommunityAccount(this.info.communityId)
    },
}
</script>

<style scoped>
  .text-color-red{
    color: red;
  }
  .text {
    font-size: 14px;
  }

  .item {
    padding: 18px 0;
  }

  .box-card {
    width: 480px;
  }
  .info{
    margin: 10px
  }
</style>